.stati-modal {
  width: 400px;
  background-color: white;
  padding: 2rem 1rem;
  text-align: center;
  // font-weight: lighter;
  max-height: 80vh;
  overflow-y: auto;
  padding-bottom: 3rem;

  display: flex;
  height: 85vh;
  // align-items: center;
  flex-direction: column;
  > *:last-child() {
    margin-bottom: 2rem;
  }

  .heading {
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
    width: 100%;
  }
  .slctHolder {
    flex-direction: column;
    display: flex;
    .react-daterange-picker__wrapper {
      border-radius: 100rem;
    }
  }
  .dataDisplay {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: 4fr repeat(5, 3em);
    font-size: 0.9em;
    text-align: center;
    gap: 1em 0.1em;
    .status {
      display: block;
      margin: auto;
      text-align: center;
      width: 10px;
      height: 10px;
      border-radius: 100rem;
      &.n {
        background-color: #aaa;
        width: 5px;
        height: 5px;
      }
    }
  }
  .error {
    color: white;
    padding: 1rem;
  }
  .slct {
    border: 1px solid rgb(169, 169, 169);
    padding: 0.5rem 1.5rem;
    width: 80%;
    margin: 0.25rem 0rem;
  }
}

.dark {
  &.stati-modal {
    & * {
      color: white;
    }
    .react-calendar {
      background-color: #222;
      border: 2px solid black;
      // width: 100%;
    }
    .react-calendar__tile:enabled:hover,
    .react-calendar__tile--hover,
    .react-calendar__tile:enabled:focus {
      background-color: #3b3b3b;
    }
    .react-calendar__tile--now {
      * {
        color: black !important;
      }
    }
    .react-calendar__navigation button:enabled:hover,
    .react-calendar__navigation button:enabled:focus {
      background-color: #2b2b2b;
    }
  }
}

