.loading-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .loading {
    filter: brightness(0);
    &.bright {
      filter: brightness(100);
    }
  }
}

.loading-container.mini {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  .loading {
    width: 1em;
    height: 100%;
    filter: brightness(0);
    &.bright {
      filter: brightness(100);
    }
  }
}
