.weekly-card {
  // padding-top: 1rem;
  // padding-bottom: 1rem;
  padding: 1rem 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  animation: popin 0.75s;
  
  &.loading {
    height: 300px;
  }

  .heading {
    text-align: center;
    border-radius: 0.25rem;
    padding: 0.4rem 0.75rem;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    span + span {
      margin-left: 0.5em;
    }
    span {
      background-color: transparent !important;
    }
    .clickable-icon {
      transition-duration: 200ms;
      &:hover {
        padding: 0.1rem;
      }
    }
  }
  .grid {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    // grid-auto-flow: row;
    text-align: center;
    margin: 1rem 0rem;
  }
  
  .icon {
    transition-duration: 200ms;
    cursor: pointer;
    &:hover {
      padding: 0.1rem;
    }
  }

  .status {
    width: 10px;
    height: 10px;
    border-radius: 1rem;
    margin: auto;
    display: block;
    transition-duration: 200ms;
    cursor: pointer;
    // padding: 3px;

    &:hover:not(.dormant) {
      width: 15px !important;
      height: 15px !important;
      // border: 2px solid rgb(201,201,201) !important;
      // background-color: transparent !important;
    }
    &.n {
      width: 8px;
      height: 8px;
      background: rgb(214, 214, 214);
      background: linear-gradient(
        99deg,
        rgba(214, 214, 214, 1) 0%,
        rgba(203, 203, 203, 1) 37%,
        rgba(193, 193, 193, 1) 87%
      );
    }
    &.dormant {
      cursor: unset;
    }
  }

  .today {
    color: white;
    border-radius: 100rem;
  }

  .prayerName {
    text-align: right;
    // height: 2rem;
  }
  .date {
    font-size: 0.75em;
    // padding: 0em 1em;
    margin-bottom: 0.25rem;
  }

  .key {
    display: flex;
    justify-content: center;
    .status {
      display: inline-block;
      margin-right: 0.5rem;
    }
    span + span {
      margin-left: 0.5rem;
    }
  }
}

@media screen and (max-width: 512px) {
  .weekly-card {
    padding-left: 0.25rem;
    padding-right: 0.25rem;
    .heading {
      padding-top: 0.7rem;
      padding-bottom: 0.7rem;
      margin: 1rem;
    }
    .day, .date {
      margin-bottom: 0.5rem;
    }
    .prayerName {
      height: 2rem;
      display: flex;
      align-items: center;
      justify-content: right;
    }
  }
}
