.register-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  // background-color: blue;
  // padding: 1rem;
  max-width: 800px;
  margin: auto;
  width: 100vw;

  .sign-modules {
    .sign.btn {
      text-align: center;
      margin-top: 1rem;
      display: block;
      width: 100%;
    }
    .sign-module {
      animation: popin 0.3s;
      display: grid;
      grid-template-columns: 25% 75%;
      // gap: 1rem;
      font-size: 1.3em;
      // width: 100px;
      animation: none;
      opacity: 1;
      .slct {
        width: 100% !important;
      }
      .sign-label {
        // align-self: center;
        margin-top: 0.5em;
      }
      .sign-input,
      .slct {
        outline: none;
        border: 0px solid black;
        border-radius: 100rem;
        font-size: 1em;
        padding: 0.5rem 2rem;
        width: auto;
        min-width: 40px;
        line-height: normal;
      }
      & + .sign-module {
        margin-top: 1rem;
      }
    }
  }

  .register-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    box-shadow: none !important;
    // color: white;
    max-width: 60%;
    background-color: #111 !important;
    color: white !important;
    min-height: 60vh !important;

    > * + * {
      margin-top: 1rem;
    }

    .heading {
      font-size: 2em;
      height: 50%;
      display: block;
      align-items: center;
      align-self: center;
      background-color: rgba(0, 0, 0, 0.3) !important;
      color: white !important;
      padding: 1rem;
      border-radius: 1rem;
      // color: inherit !important;
      width: 100%;
      text-align: center;
    }

    select {
      padding: 0.5rem;
      border-radius: 100rem;
      outline: none;
      font-size: 1em;
      width: 60%;
    }

    button {
      font-size: 1.1em;
      padding: 0.5rem 1rem;
      border-radius: 100rem;
      transition-duration: 300ms;
      width: 60%;
      &:hover {
        background-color: #ddd;
      }
      &.dormant {
        border-radius: 0.5rem;
      }
      &.dormant:hover {
        background-color: #eee;
        cursor: unset;
      }
      & + button {
        margin-left: 1rem;
      }
    }
  }
  div.error {
    text-align: center;
    max-width: 100%;
    padding: 1rem;
    border-radius: 0.25rem;
    width: 100%;
    animation: popin 0.4s;
  }
  .btn.error {
    padding: 0.5rem 2rem;
    display: inline-block;
  }
}

@media screen and (max-width: 820px) {
  .register-container {
    width: 100vw;
    .register-card {
      max-width: 80%;
    }
  }
}

@media screen and (max-width: 512px) {
  .register-container {
    // height: 100vh;
    height: unset;
    min-height: unset;
    width: 100%;
    .register-card {
      height: 100%;
      width: 100%;
      max-width: unset;
      border-radius: 0rem;
      .btn {
        width: 100%;
      }
    }
  }
}
