.optionSwitch {
  display: inline-block;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 100rem;
  z-index: 1;
}
.optionContainer {
  display: inline-block;
  span {
    display: inline-block;
  }
  cursor: pointer;
}
.optionContainer input.option {
  display: none;
}
.optionContainer input.option + span {
  padding: 0.5rem 2rem;
  transition-duration: 1ms;
  text-align: center;
}
@media screen and (max-width: 512px) {
  .optionContainer input.option + span {
    padding: 0.5rem 1.5rem;
    font-size: 0.8em;
  }
}
.optionSwitch .optionContainer:first-child input.option + span {
  border-radius: 100rem 0rem 0rem 100rem;
}
.optionSwitch .optionContainer:last-child input.option + span {
  border-radius: 0rem 100rem 100rem 0rem;
}
.optionContainer input.option + span:hover {
  transition-duration: 200ms;
  background-color: rgba(255, 255, 255, 0.2);
}
.optionContainer input.option:checked + span {
  transition-duration: 300ms;
  background-color: #fff;
  color: #222;
}
.optionSwitch.dark .optionContainer input.option + span:hover {
  background-color: rgba(0, 0, 0, 0.2);
}
.optionSwitch.dark .optionContainer input.option:checked + span {
  background-color: #222;
  color: #fff;
}
